@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700,900&display=swap);

.game_field__form {
  align-self: center;
  margin-bottom: 40px;
}

.form_format {
  padding: 10px;
  margin: 0 5px;
  color: #909399;
  border-radius: 3px;
  outline: none;
  border: none;
  background: #c2c9cc;
}

.game_field__form input {
  background: #f5f4f2;
}

.form_button {
  cursor: pointer;
  width: 115px;
  text-transform: uppercase;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  color: white;
  background: #8b9599;
}

.form_button:hover {
  background: green;
}

.restar_button {
  font-size: 14px;
  text-transform: none;
}

td {
  width: 40px;
  height: 40px;
  border: 1px solid lightgray;
}

.choose_td {
  background: #86d2f0;
  cursor: pointer;
}

.computer_td {
  background: #b32b2b;
}

.player_td {
  background: #168514;
}

.game_field__table {
  align-self: center;
}

table {
  border-collapse: collapse;
}

.game_field__counter {
  align-self: center;
  margin-bottom: 40px;
  width: 80%;
  display: flex;
  flex-direction: column;
}

.counter__points_for_win {
  align-self: center;
}

.conter__curr_points {
  display: flex;
  justify-content: center;
}

.conter__curr_points p {
  margin: 0 9%;
}

#modal-root {
  position: relative;
  z-index: 999;
}

.game_field__modal {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.modal__alert {
  margin-top: 35vh;
  color: white;
  font-size: 60px;
  margin-bottom: 50px;
}

.modal_button {
  cursor: pointer;
  width: 200px;
  height: 50px;
  color: white;
  background: #5d6263;
  text-transform: uppercase;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  border: none;
  border-radius: 5px;
  font-size: 22px;
}

.modal_button:hover {
  background: green;
}

.game_field {
  width: 60%;
  display: flex;
  flex-direction: column;
  padding-top: 100px;
  border-right: 2px solid rgba(217, 217, 217, 0.5);
}

.game_field__warning {
  align-self: center;
  color: red;
  bottom: 380px;
}

.winner_board__item {
  display: flex;
  list-style: none;
  padding: 15px 10px;
  background: #c2c9cc;
  margin: 3px;
  border-radius: 3px;
}

.winners_board {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 100px;
  margin: 0 auto;
}

.title {
  margin: 0;
  font-weight: 500;
}

.winners_board__list {
  margin-top: 20px;
  min-width: 300px;
}

