#modal-root {
  position: relative;
  z-index: 999;
}

.game_field__modal {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.modal__alert {
  margin-top: 35vh;
  color: white;
  font-size: 60px;
  margin-bottom: 50px;
}

.modal_button {
  cursor: pointer;
  width: 200px;
  height: 50px;
  color: white;
  background: #5d6263;
  text-transform: uppercase;
  transition: 0.5s;
  border: none;
  border-radius: 5px;
  font-size: 22px;
}

.modal_button:hover {
  background: green;
}
