.game_field__form {
  align-self: center;
  margin-bottom: 40px;
}

.form_format {
  padding: 10px;
  margin: 0 5px;
  color: #909399;
  border-radius: 3px;
  outline: none;
  border: none;
  background: #c2c9cc;
}

.game_field__form input {
  background: #f5f4f2;
}

.form_button {
  cursor: pointer;
  width: 115px;
  text-transform: uppercase;
  transition: 0.5s;
  color: white;
  background: #8b9599;
}

.form_button:hover {
  background: green;
}

.restar_button {
  font-size: 14px;
  text-transform: none;
}
